<script>

export default {
    data() {
        return {
            ruleForm:{},
						select_part: [],
            productData:[],
            houFiveData:[],
            tableData6: [],
						nanDuDengJi: '',
						itemOrder: {
							project:{
								projectName:""
							},
							processUnit:{
								clientCompanyName:""
							}
						},
						ruleForm: {
							orderName: '',
							addUserId: '',
							addUserName: '',
							projectId: '',
							hasRemark: false,
							picUrls: '',
							urgency: '0',
							remark: '',
							realityArea: '0',
							machiningType: ''
						},
						techId: false,
						lanMoList:[],
						qrCode: '',
						elform: {},
						imageUrls: []
        };
    },
    methods: {
			autoTextarea() {
			 var extra = 0,   //设置光标与输入框保持的距离(默认0)
　　　　maxHeight = 1000; //设置最大高度(可选)
　　　　var _that = this;
　　　　var isFirefox = !!document.getBoxObjectFor || 'mozInnerScreenX' in window,
　　　　isOpera = !!window.opera && !!window.opera.toString().indexOf('Opera');
　　　　　var paddingTop,paddingBottom
			 var style,_length,valueLength,stylHeight,scrollHeight,currHeight;

				 this.$nextTick(function () {
						 if(this.isHeight){
							 this.isHeight =  false
							 this.minHeight = parseFloat(window.getComputedStyle(this.$refs.elememt).height)
						 }

						 paddingTop = parseInt(window.getComputedStyle(this.$refs.elememt).paddingTop)
						 paddingBottom = parseInt(window.getComputedStyle(this.$refs.elememt).paddingBottom)
						 style = this.$refs.elememt.style
						 _length = this.$refs.elememt._length
						 valueLength = this.$refs.elememt.value.length
						 stylHeight = this.$refs.elememt.style.height
						 scrollHeight = this.$refs.elememt.scrollHeight
						 currHeight = this.$refs.elememt.currHeight

							change()
				 })

			 function change(){
				 var  height, padding = 0;

				 if (_length === valueLength) return;
						 _length = valueLength;

				 if (!isFirefox && !isOpera) {
					 padding = paddingTop + paddingBottom;
				 };
				 stylHeight = _that.minHeight + 'px';  //30px
				 if (scrollHeight > _that.minHeight) {
					 if (maxHeight && scrollHeight > maxHeight) {
						 height = maxHeight - padding;
						 style.overflowY = 'hidden';
					 } else {
						 height = scrollHeight - padding;      //undefined 30 9
						 style.overflowY = 'hidden';
					 };

					 style.height = height + extra + 'px';
					 currHeight = parseInt(style.height);
				 };
			 }
		 },
			addSelected(item) {
				let self = this
				let sel = self.select_part
				if (sel.length === 0) {
					if (item.collectPartinfoEntity.picurl instanceof Array) {
						sel.push(item);
					} else {
						item.collectPartinfoEntity.picurl = self.splitUrl(item.collectPartinfoEntity)
						sel.push(item);
					}
				} else {
					let flag = true
					for (let i = 0; i < sel.length; i++) {
						if (sel[i].collectPartinfoEntity.id === item.collectPartinfoEntity.id) {
							flag = false;
							break;
						}
					}
					if (flag) {
						if (item.collectPartinfoEntity.picurl instanceof Array) {
							sel.push(item);
						} else {
							item.collectPartinfoEntity.picurl = self.splitUrl(item.collectPartinfoEntity)
							sel.push(item);
						}
					}
				}
			},
			splitUrl(item) {
				let picurl = item.picUrls;
				let urlArray = [];
				if (picurl != "undefined" && picurl != "") {
					urlArray = picurl.split('|');
				}
				return urlArray;
			},
			getOrderInfo(urlId) {
				let self = this;
				this.getRequest("/config/info?orderId=" + urlId).then(resp => {
					if (resp && resp.status == 200 && resp.data.code == 0) {
						console.log(resp);
						let orderFlow = resp.data.data.orderinfo.orderFlow
						if(orderFlow != '' && orderFlow!=='undefined'){
							this.orderFlow = JSON.parse(orderFlow.flowJson)
						}
						this.nanDuDengJi = resp.data.data.orderinfo.nanDuDengJi
						this.itemOrder = resp.data.data.orderinfo;
						this.ruleForm = resp.data.data.orderinfo;
						this.techId = resp.data.data.orderinfo.techId;
						let techCardList = resp.data.data.orderinfo.techCardList;
						this.lanMoList = resp.data.data.orderinfo.lanMoList;

						this.qrCode = resp.data.data.orderinfo.qrCode;
						this.exform = resp.data.data.orderinfo.controlOrderFrom;
						let moulds = resp.data.data.orderinfo.mouldInfoList;
						if ( moulds.length > 0) {
							for (let i = 0; i < moulds.length; i++) {
								let part = {
									collectPartinfoEntity: {}
								}
								part.collectPartinfoEntity = moulds[i];
								this.addSelected(part);
							}
						}
						if(resp.data.data.orderinfo.picUrls !== ""){
							this.imageUrls = this.splitUrl(resp.data.data.orderinfo);
						}
					} else {
						console.log("获取订单详情失败");
					}
				});
			}
    },
    created: function() {
      this.urlId = this.$route.params.id;
      this.getOrderInfo(this.urlId);
    }
};
</script>

