.body{margin-top: 0px !important;font-size:10px;height:auto;font-family: Arial;overflow:auto !important;}
#app{margin-top: 0px !important;}
.contents{width:720px;border:0px solid black;margin:0 auto;padding-top:5px; overflow:auto !important;}
.head_title{text-align:center	;font-size:20px;margin-bottom:5px;}
