}

.tb-cls {margin-top:0px;margin-left:0px;}
.tb-cls table{border-collapse:collapse;}
.tb-cls table th{height:20px;line-height:20px;color:#000;width: 100px;border:1px solid;}
.tb-cls-th-left{border-left:1px solid #514F49;}
.tb-cls-th-right{border-right:1px solid #514F49;}
.tb-cls table td{	height:20px;line-height:20px;border:1px solid;padding:0px 5px;}


.logs {border:1px solid #666666;margin:2px;padding:20px 10px;background-color:#FFF;}
.logs table {width:100%;}
.logs td {border-bottom:1px dotted #EEE;line-height:24px;}

.logs1 {border:1px solid #666666;margin:2px;padding:20px 10px;background-color:#FFF;}
.logs1 table {width:100%;}
.logs1 td {border-bottom:1px dotted #EEE;line-height:24px;}


.btn {height:25px;min-width:70px;
			line-height:20px;
			padding:2px 10px;
			text-align:center;
			margin-top:5px;
			/* background:url('../images/bg_buttom.png') repeat-x; */
			cursor:pointer;
			color:#FFF;
			display:inline-block;}
.handle-banner {
	height:auto;
	text-align:left;
	left:0px;top:0px;
	background-color:#F2F2F2;
	padding:5px 0px 5px 150px;
	z-index:900;
}
.l_tr_title{
		text-align:right;
		display:inline-block;
		vertical-align:top;
		width:72px;!important
		margin:0px;
		padding-bottom:5px;
}
.l_tr_content{
		text-align:left;
		display:inline-block;
		vertical-align:top;
		width:100px;
		margin:0px;
		padding-bottom:5px;
}

.l_tr_content_full{
		text-align:left;
		display:inline-block;
		vertical-align:top;
		width:100px;
		margin:0px;
		padding-bottom:5px;
}
</style>



